const SELECTORS = {
    TOGGLE: "[data-accordion-toggle]"
};

const accordion = (() => {
    const init = () => {
        const accordionToggleBtns = document.querySelectorAll(SELECTORS.TOGGLE);

        if (accordionToggleBtns) {
            accordionToggleBtns.forEach(btn => {
                const dataVal = btn.dataset.accordionToggle;

                if (dataVal) {
                    const accordion = document.getElementById(dataVal);

                    if (accordion) {
                        btn.addEventListener("click", e => {
                            e.preventDefault();
                            accordion.classList.toggle("accordion-open");
                        });
                    } else {
                        console.warn(`No alert with with id ${dataVal} was found, make sure the attribute data-alert-close contains the correct id.`);
                    }
                }
            });
        }
    };

    return { init };
})();

export default accordion;
